import Vue from 'vue'
import VueRouter from 'vue-router'

// import Login from '../components/Login'
// import Home from '../components/Home'
// import Welcome from '../components/Welcome'
const Login = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Login')
const Home = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Home')
const Welcome = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Welcome')

// import Users from '../components/users/Users'
const Users = () => import(/* webpackChunkName: "users" */ '../components/users/Users')

// import Rights from '../components/rights/Rights'
// import Roles from '../components/rights/Roles'
const Rights = () => import(/* webpackChunkName: "rights_roles" */ '../components/rights/Rights')
const Roles = () => import(/* webpackChunkName: "rights_roles" */ '../components/rights/Roles')

// import Cate from '../components/goods/Cate'
// import Params from '../components/goods/Params'
// import Goods from '../components/goods/Goods'
// import AddPage from '../components/goods/AddPage'
const Cate = () => import(/* webpackChunkName: "cate_params_goods_addpage" */ '../components/goods/Cate')
const Params = () => import(/* webpackChunkName: "cate_params_goods_addpage" */ '../components/goods/Params')
const Goods = () => import(/* webpackChunkName: "cate_params_goods_addpage" */ '../components/goods/Goods')
const AddPage = () => import(/* webpackChunkName: "cate_params_goods_addpage" */ '../components/goods/AddPage')

// import Order from '../components/order/Order'
const Order = () => import(/* webpackChunkName: "order" */ '../components/order/Order')

// import Statistic from '../components/statistic/Statistic'
const Statistic = () => import(/* webpackChunkName: "statistic" */ '../components/statistic/Statistic')

// 解决elementUI的Navigation点击导航跳转报错问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function (location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: Login },
  {
    path: '/home',
    component: Home,
    redirect: '/welcome',
    children: [
      { path: '/welcome', component: Welcome },
      { path: '/users', component: Users },
      { path: '/rights', component: Rights },
      { path: '/roles', component: Roles },
      { path: '/categories', component: Cate },
      { path: '/params', component: Params },
      { path: '/goods', component: Goods },
      { path: '/goods/add', component: AddPage },
      { path: '/orders', component: Order },
      { path: '/reports', component: Statistic }

    ]
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  // 1、访问其他页面，如果没有登录，跳转至登录页
  if (to.path !== '/login') {
    if (!window.sessionStorage.getItem('token')) return next('/login')
    next()
  }
  next()
})

export default router
